  border-radius: 12px;
  background: #fff;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
  transition: 0.3s;
}
.item-more__inner.active {
  opacity: 1;
  visibility: visible;
}
.item-more__close {
  position: absolute;
  right: 0;
  top: 10px;
  width: 20px;
  height: 20px;
  z-index: 5;
  cursor: pointer;
}
.item-more__close:before, .item-more__close:after {
  content: "";
  display: inline-block;
  width: 1px;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  background: #2C2F31;
  transition: 0.3s;
}
.item-more__close:after {
  transform: rotate(45deg);
}
.item-more__close::before {
  transform: rotate(-45deg);
}
